@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#title {
    text-align: center;
    border-bottom: 1px solid #d3d3d3;
    position: fixed;
    top: 0;
    left: 0;
    width: 260px;
    z-index: 99999;
    background-color: #fff;
    padding-left:10px;
    padding-right:10px;
    box-sizing: border-box;
}

#title h1 {
    font-weight: 100;
    font-size: 2.2em;
    margin-bottom: 0;
    font-family: sans-serif;
}

#title p {
    margin-top: 2px;
    margin-bottom:10px;
    opacity: .3;
}

#content {
    margin-top:100px;    
}

#sideBar {
    border-right:1px solid #d3d3d3;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 10px, rgba(0, 0, 0, 0.23) 0px 3px 10px;
    height: 100vh;   
    overflow: scroll;
    padding-left: 10px;
    padding-right:10px;
    padding-bottom:200px;
    box-sizing: border-box;
}

#download {
    float:"right";
    position: fixed;
    bottom:50px;
    right:10px;
}
.controlLabel {
    opacity: .6
}

h2 {
    font-weight: 300;
}
/* Footer */
footer {
    width: 100%;
    background-color: white;
    position: fixed;
    bottom: 0px;
    z-index: 9999;
}

.footer-copyright {
    border-top: 1px solid #d3d3d3;
    margin-top: 10px;
    padding: 5px;
    opacity: .5;
}

.right {
    float: right;
}

/* From materialize.css */

footer .container {
    margin: 0 auto;
    max-width: 1280px;
    width: 90%;
  }
  
  @media only screen and (min-width: 601px) {
    footer .container {
      width: 85%;
    }
  }
  
  @media only screen and (min-width: 993px) {
    footer .container {
      width: 80%;
      padding-left:263px;
    }
  }
